// header,  logo
import {Component, Input, OnInit} from '@angular/core';

import {RouterService} from 'app/core/services/router.service';
import {SystemService} from 'app/core/services/system.service';

@Component({
  selector: 'app-main-header-logo',
  templateUrl: './main-header-logo.component.html',
  styleUrls: ['./main-header-logo.component.css']
})
export class MainHeaderLogoComponent implements OnInit {

  @Input() returnBtnShow: boolean;  // 
  @Input() doubleBtnShow: boolean;  // 
  @Input() timerShow: boolean;  // 
  @Input() timerMax: number; // 

  constructor(private routerService: RouterService,
              private systemService: SystemService) {
  }

  ngOnInit() {
  }

  goToReturn(): void {
    this.systemService.backMainMenu();
  }

  goToPrev(): void {
    this.routerService.back();
  }

  // 
  timerCompleted(event: number) {
    this.systemService.backMainMenu();
  }
}



