// 

import {RouterInfo} from 'app/core/objects/router-info';

export class InfoMessage {

  static readonly statusNormal: number = 1; // 
  static readonly statusError: number = 2; // 

  data: string; // 
  status: number; // 
  routerInfo: RouterInfo; // 
  autoCloseTime = 0; // 0
  noRouter = false; // UI true 

  constructor(data: string,
              status: number = 1,
              routerInfo: RouterInfo = null) {
    this.data = data;
    this.status = status;
    this.routerInfo = routerInfo;
  }
}



