// 

import {ModeLibrary} from 'app/core/libs/mode-library';
import {Observable, Subscriber} from 'rxjs/Rx';

export class DeviceParameter {
  private static _isShellDebugMode = false; // shellapptrue 
  private static _isUserDebugMode = false; // 
  private static _isReadCardDebugMode = false; // 
  private static _isInfoDebugMode = false; // 

  private static isInfoDebugModeObservable: Observable<boolean>; // 
  private static isInfoDebugModeSubscriber: Subscriber<boolean>;

  static get isInfoDebugMode(): boolean {
    return this._isInfoDebugMode;
  }

  static get isReadCardDebugMode(): boolean {
    return this._isReadCardDebugMode;
  }

  static get isShellDebugMode(): boolean {
    return this._isShellDebugMode;
  }

  static get isUserDebugMode(): boolean {
    return this._isUserDebugMode;
  }

  /**
   * 
   * @param {string} mode
   */
  public static setMode(mode: string): void {
    switch (mode) {
      case ModeLibrary.modeProduction:
        DeviceParameter.enableProductionMode();
        break;

      case ModeLibrary.modeDeviceDebug:
        DeviceParameter.enableDeviceDebugMode();
        break;

      case ModeLibrary.modeUserDebug:
        DeviceParameter.enableUserDebugMode();
        break;

      case ModeLibrary.modeShellWebDebug:
        DeviceParameter.enableShellWebDebugMode();
        break;

      default:
        DeviceParameter.enableWebDebugMode();
        break;
    }
  }

  public static toString(): string {
    return 'DeviceParameter... isShellDebugMode:['
      + DeviceParameter._isShellDebugMode
      + '], isReadCardDebugMode:['
      + DeviceParameter._isReadCardDebugMode
      + '], isUserDebugMode:['
      + DeviceParameter._isUserDebugMode
      + '], isInfoDebugMode:['
      + DeviceParameter._isInfoDebugMode
      + ']';
  }

  /**
   * web
   */
  public static enableWebDebugMode(): void {
    console.log('run with Web Debug mode');
    DeviceParameter._isShellDebugMode = true;
    DeviceParameter._isReadCardDebugMode = true;
    DeviceParameter._isUserDebugMode = true;
    DeviceParameter._isInfoDebugMode = true;
    DeviceParameter.changeIsInfoDebugMode(DeviceParameter._isInfoDebugMode);
  }

  /**
   * webwebview
   */
  public static enableShellWebDebugMode(): void {
    console.log('run with Web Debug mode');
    DeviceParameter._isShellDebugMode = false;
    DeviceParameter._isReadCardDebugMode = true;
    DeviceParameter._isUserDebugMode = true;
    DeviceParameter._isInfoDebugMode = true;
    DeviceParameter.changeIsInfoDebugMode(DeviceParameter._isInfoDebugMode);
  }

  /**
   * 
   */
  private static enableProductionMode(): void {
    DeviceParameter._isShellDebugMode = false;
    DeviceParameter._isReadCardDebugMode = false;
    DeviceParameter._isUserDebugMode = false;
    DeviceParameter._isInfoDebugMode = false;
    DeviceParameter.changeIsInfoDebugMode(DeviceParameter._isInfoDebugMode);
  }

  /**
   * 
   */
  private static enableUserDebugMode(): void {
    DeviceParameter._isShellDebugMode = false;
    DeviceParameter._isReadCardDebugMode = false;
    DeviceParameter._isUserDebugMode = true;
    DeviceParameter._isInfoDebugMode = true;
    DeviceParameter.changeIsInfoDebugMode(DeviceParameter._isInfoDebugMode);
  }

  /**
   * 
   */
  private static enableDeviceDebugMode(): void {
    DeviceParameter._isShellDebugMode = false;
    DeviceParameter._isReadCardDebugMode = false;
    DeviceParameter._isUserDebugMode = false;
    DeviceParameter._isInfoDebugMode = true;
    DeviceParameter.changeIsInfoDebugMode(DeviceParameter._isInfoDebugMode);
  }

  /**
   * Observable
   * @returns {Observable<boolean>}
   */
  public static getInfoDebugMode(): Observable<boolean> {
    return DeviceParameter.isInfoDebugModeObservable;
  }

  public static init() {
    console.log('------------- 0')
    DeviceParameter.isInfoDebugModeObservable = new Observable(
      subscriber => {
        DeviceParameter.isInfoDebugModeSubscriber = subscriber;
      }
    );
  }

  /**
   * debugMode
   * @param {boolean} isInfoDebugMode
   */
  private static changeIsInfoDebugMode(isInfoDebugMode: boolean) {
    console.log('------------- 1')
    if (DeviceParameter.isInfoDebugModeSubscriber) {
      console.log('------------- 2')
      DeviceParameter.isInfoDebugModeSubscriber.next(isInfoDebugMode);
    }
  }
}



