import {CoverItem} from 'app/core/objects/cover-item';
import {PathLibrary} from 'app/core/libs/path-library';

export class CoverItemLibrary {

  public static readonly type1: number = 1; // 
  public static readonly type2: number = 2; // 
  public static readonly type3: number = 3; // 
  public static readonly type4: number = 4; // 
  public static readonly type5: number = 5; // 
  public static readonly type6: number = 6; // 
  public static readonly type7: number = 7; // 

  public static itemData(): CoverItem[] {
    const coverData: CoverItem[] = [];
    coverData.push(new CoverItem(1, 'assets/images/icon_outpatient-payment.png', '',
      PathLibrary.outpatientPaymentRecord));
    coverData.push(new CoverItem(2, 'assets/images/icon_hospital-pay-cost.png', '',
      PathLibrary.inpatientLogin));
    coverData.push(new CoverItem(3, 'assets/images/icon_one-card-recharge.png', '',
      PathLibrary.paymentAmountSelect));
    // coverData.push(new CoverItem(4, 'assets/images/4.png',  '', PathLibrary.video));
    // coverData.push(new CoverItem(4, 'assets/images/4.png',  '', PathLibrary.login));
    return coverData;
  }

}



