// 

import {Injectable} from '@angular/core';
import {Observable, Subscriber, Subscription} from 'rxjs/Rx';
import {TimerObservable} from 'rxjs/observable/TimerObservable';

import {BaseService} from 'app/core/bases/base-service';

@Injectable()
export class TimerService extends BaseService {

  private readonly ONE_SECOND: number = 1000; // 1
  private readonly MAX_SECOND: number = 10; // X

  private currentTime: number;  // 

  private timerOutsideObservable: Observable<number>; // 
  private timerOutsideSubscriber: Subscriber<number>;

  private timerInsideObservable: Observable<number>; // 
  private timerInsideSubscription: Subscription;

  private maxTime: number;  // 

  constructor() {
    super();
  }

  init() {
    this.maxTime = this.MAX_SECOND;

    this.timerOutsideObservable = new Observable(
      subscriber => {
        this.timerOutsideSubscriber = subscriber;
      }
    );
    this.timerInsideObservable = TimerObservable.create(0, this.ONE_SECOND);
  }

  // 
  start(maxTime: number = this.maxTime): Observable<number> {

    // reset
    this.maxTime = maxTime;
    this.currentTime = this.maxTime;

    if (this.timerInsideSubscription) {
      this.timerInsideSubscription.unsubscribe();
    }

    this.timerInsideSubscription = this.timerInsideObservable.subscribe(
      t => {
        // console.log('t: ', t);

        if (t <= this.maxTime) {
          this.change(this.timerOutsideSubscriber);
        } else {
          // stop it
          this.timerInsideSubscription.unsubscribe();
        }
      });

    return this.timerOutsideObservable;
  }

  //  Observable  unsubscribe 
  stop() {
    this.timerInsideSubscription.unsubscribe();
  }

  // 
  private change(observer: Subscriber<number>) {
    observer.next(this.currentTime);

    this.currentTime--;
    if (this.currentTime <= -1) {
      observer.complete();
    }
  }

}



