// 

import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Rx';

import {HttpService} from 'app/core/public/http.service';
import {ResponseDataWrap} from 'app/core/objects/response-data-wrap';

import {ServerApiLibrary} from 'app/protocol/server/server-api-library';

import {InputDeviceLogin} from 'app/protocol/server/input-device-login';
import {InputQueryPayChannelList} from 'app/protocol/server/input-query-pay-channel-list';
import {InputQueryOutPatientInfo} from 'app/protocol/server/input-query-out-patient-info';
import {InputQueryInHospitalPatient} from 'app/protocol/server/input-query-in-hospital-patient';
import {InputCreateOrder} from 'app/protocol/server/input-create-order';
import {InputUnifiedOrder} from 'app/protocol/server/input-unified-order';
import {InputQueryOrderStatus} from 'app/protocol/server/input-query-order-status';
import {InputQueryPaidRecord} from 'app/protocol/server/input-query-paid-record';
import {InputQueryOutpatientPayRecords} from 'app/protocol/server/input-query-outpatient-pay-records';

@Injectable()
export class ServerRequestService {

  constructor(private httpService: HttpService) {
  }

  // 
  deviceLogin(inputDeviceLogin: InputDeviceLogin): Observable<ResponseDataWrap> {
    return this.httpService
      .requestServer(
        ServerApiLibrary.authInfo.serverURL + ServerApiLibrary.deviceLogin,
        {
          'requestToken': ServerApiLibrary.authInfo.token,
          'requestData': inputDeviceLogin
        }
      );
  }

  // 
  queryPayChannelList(inputQueryPayChannelList: InputQueryPayChannelList): Observable<ResponseDataWrap> {
    return this.httpService
      .requestServer(
        ServerApiLibrary.authInfo.serverURL + ServerApiLibrary.queryPayChannelList,
        {
          'requestToken': ServerApiLibrary.authInfo.token,
          'requestData': inputQueryPayChannelList
        }
      );
  }

  // 
  queryOutPatientInfo(inputQueryOutPatientInfo: InputQueryOutPatientInfo): Observable<ResponseDataWrap> {
    return this.httpService
      .requestServer(
        ServerApiLibrary.authInfo.serverURL + ServerApiLibrary.queryOutPatientInfo,
        {
          'requestToken': ServerApiLibrary.authInfo.token,
          'requestData': inputQueryOutPatientInfo
        }
      );
  }

  // 
  queryInHospitalPatient(inputQueryInHospitalPatient: InputQueryInHospitalPatient): Observable<ResponseDataWrap> {
    return this.httpService
      .requestServer(
        ServerApiLibrary.authInfo.serverURL + ServerApiLibrary.queryInHospitalPatient,
        {
          'requestToken': ServerApiLibrary.authInfo.token,
          'requestData': inputQueryInHospitalPatient
        }
      );
  }

  // 
  createOrder(inputCreateOrder: InputCreateOrder): Observable<ResponseDataWrap> {
    return this.httpService
      .requestServer(
        ServerApiLibrary.authInfo.serverURL + ServerApiLibrary.createOrder,
        {
          'requestToken': ServerApiLibrary.authInfo.token,
          'requestData': inputCreateOrder
        }
      );
  }

  // 
  unifiedOrder(inputUnifiedOrder: InputUnifiedOrder): Observable<ResponseDataWrap> {
    return this.httpService
      .requestServer(
        ServerApiLibrary.authInfo.serverURL + ServerApiLibrary.unifiedOrder,
        {
          'requestToken': ServerApiLibrary.authInfo.token,
          'requestData': inputUnifiedOrder
        }
      );
  }

  // 
  queryOrderStatus(inputQueryOrderStatus: InputQueryOrderStatus): Observable<ResponseDataWrap> {
    return this.httpService
      .requestServer(
        ServerApiLibrary.authInfo.serverURL + ServerApiLibrary.queryOrderStatus,
        {
          'requestToken': ServerApiLibrary.authInfo.token,
          'requestData': inputQueryOrderStatus
        }
      );
  }

  // 
  queryPaidRecord(inputQueryPaidRecord: InputQueryPaidRecord): Observable<ResponseDataWrap> {
    return this.httpService
      .requestServer(
        ServerApiLibrary.authInfo.serverURL + ServerApiLibrary.queryPaidRecord,
        {
          'requestToken': ServerApiLibrary.authInfo.token,
          'requestData': inputQueryPaidRecord
        }
      );
  }

  // 
  queryOutpatientPayRecords(inputQueryOutpatientPayRecords: InputQueryOutpatientPayRecords): Observable<ResponseDataWrap> {
    return this.httpService
      .requestServer(
        ServerApiLibrary.authInfo.serverURL + ServerApiLibrary.queryOutpatientPayRecords,
        {
          'requestToken': ServerApiLibrary.authInfo.token,
          'requestData': inputQueryOutpatientPayRecords
        }
      );
  }


}



