// token

import {Injectable} from '@angular/core';
import {Observable, Subscription} from 'rxjs/Rx';
import {TimerObservable} from 'rxjs/observable/TimerObservable';

import {BaseService} from 'app/core/bases/base-service';
import {AuthService} from 'app/core/services/auth.service';
import {LogService} from 'app/core/services/log.service';
import {ShellRequestService} from 'app/protocol/shell/shell-request.service';

@Injectable()
export class BackService extends BaseService {

  private readonly timerInitialDelay: number = 600000; // 10
  private readonly timerPeriodCheckVersion: number = 600000; // 10
  private readonly timerPeriodRenewToken: number = 43200000; // token12

  private checkVersionTimerObservable: Observable<number>; // 
  private checkVersionTimerSubscription: Subscription;

  private renewTokenTimerObservable: Observable<number>; // token
  private renewTokenTimerSubscription: Subscription;

  constructor(private authService: AuthService,
              private shellRequestService: ShellRequestService) {
    super();
  }

  init() {
  }

  // 
  start() {
    this.checkVersionTimerObservable = TimerObservable.create(this.timerInitialDelay, this.timerPeriodCheckVersion);
    this.checkVersionTimerSubscription = this.checkVersionTimerObservable.subscribe(
      t => {
        LogService.debug('try to check version ...');
        this.checkVersion();
      });

    this.renewTokenTimerObservable = TimerObservable.create(this.timerPeriodRenewToken, this.timerPeriodRenewToken);
    this.renewTokenTimerSubscription = this.renewTokenTimerObservable.subscribe(
      t => {
        LogService.debug('try to renew token ...');
        this.renewToken();
      });
  }

  // 
  stop() {
    if (this.checkVersionTimerSubscription) {
      this.checkVersionTimerSubscription.unsubscribe();
    }
    if (this.renewTokenTimerSubscription) {
      this.renewTokenTimerSubscription.unsubscribe();
    }
  }

  // 
  private checkVersion() {
    // this.authService.checkVersionFromServer();
    // 
    this.shellRequestService.web2ShellUpdateVersion();
  }

  // token
  private renewToken() {
    this.authService.renewTokenFromServer();
  }

}



