import {AuthInfo} from 'app/core/objects/auth-info';

export class ServerApiLibrary {

  private static _authInfo: AuthInfo = new AuthInfo();

  static get authInfo(): AuthInfo {
    return this._authInfo;
  }

  static set authInfo(value: AuthInfo) {
    this._authInfo = value;
  }

  // -----------

  public static readonly deviceLogin = 'minipay/auth/login'; // 
  public static readonly queryPayChannelList = 'minipay/channel/qryPayChannelList'; // 
  public static readonly queryOutPatientInfo = 'minipay/patient/qryOutPatientInfo'; // 
  public static readonly queryInHospitalPatient = 'minipay/patient/qryInhospitalPatient'; // 
  public static readonly createOrder = 'minipay/order/createOrder'; // 
  public static readonly unifiedOrder = 'minipay/order/UnifiedOrder'; // 
  public static readonly queryOrderStatus = 'minipay/order/qryOrderStatus'; // 
  public static readonly queryPaidRecord = 'minipay/order/qryPaidRecord'; // 
  public static readonly queryOutpatientPayRecords = 'minipay/outpatient/qryOutpatientPayRecords'; // 

}



