// 
import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';

@Component({
  selector: 'app-virtual-keyboard',
  templateUrl: './virtual-keyboard.component.html',
  styleUrls: ['./virtual-keyboard.component.css']
})
export class VirtualKeyboardComponent implements OnInit {
  serialNumber = '';

  @Input() virtualKeyboardShow: boolean;
  @Output() virtualKeyboardShowChange = new EventEmitter<boolean>();
  @Output() getInpatientInfo = new EventEmitter<string>();

  constructor() {
  }

  ngOnInit() {
  }

  // 
  virtualKeyboardTap(keyNumber): void {
    if (this.serialNumber === '') {
      this.serialNumber = keyNumber;
    } else {
      this.serialNumber += keyNumber;
    }
  }

  // 
  deleteTap(): void {
    if (this.serialNumber.length === 1) {
      this.serialNumber = '';
    } else if (this.serialNumber !== '') {
      this.serialNumber = this.serialNumber.substring(0, this.serialNumber.length - 1);
    }
  }

  // 
  confirmTap(): void {
    if (this.serialNumber !== ''){
      this.getInpatientInfo.emit(this.serialNumber);
    }
    this.closeVirtualKeyboard();
  }

  // 
  cancelTap(): void {
    this.closeVirtualKeyboard();
  }

  // 
  clearTap(): void {
    this.serialNumber = '';
  }

  // 
  closeVirtualKeyboard(): void {
    this.virtualKeyboardShow = false;
    this.virtualKeyboardShowChange.emit(this.virtualKeyboardShow);
  }
}



