// 

import {Injectable} from '@angular/core';
import {MatDialog} from '@angular/material';

import {ShellRequestService} from 'app/protocol/shell/shell-request.service';
import {AuthService} from 'app/core/services/auth.service';
import {BackService} from 'app/core/services/back.service';
import {InfoService} from 'app/core/services/info.service';
import {LogService} from 'app/core/services/log.service';
import {TimerService} from 'app/core/services/timer.service';
import {RouterService} from 'app/core/services/router.service';
import {UserService} from 'app/core/services/user.service';
import {ErrorService} from 'app/core/services/error.service';
import {LoadingService} from 'app/core/services/loading.service';

import {PathLibrary} from 'app/core/libs/path-library';
import {InfoMessage} from 'app/core/objects/info-message';
import {DeviceParameter} from 'app/core/public/device-parameter';

@Injectable()
export class SystemService {

  constructor(private shellRequestService: ShellRequestService,
              private authService: AuthService,
              private backService: BackService,
              private errorService: ErrorService,
              private loadingService: LoadingService,
              private routerService: RouterService,
              private infoService: InfoService,
              private logService: LogService,
              private timerService: TimerService,
              private userService: UserService,
              private dialog: MatDialog) {
  }

  init() {
    // set device parameter
    DeviceParameter.init();
    // 
    // DeviceParameter.enableWebDebugMode(); // 

    // init all service
    this.shellRequestService.init();
    this.logService.init();
    this.infoService.init();
    this.routerService.init();
    this.authService.init();
    this.errorService.init();
    this.loadingService.init();
    this.timerService.init();
    this.backService.init();

    //  ShellRequestService 
    this.shellRequestService.initLog()
      .subscribe(
        data => this.getLogFromShellRequestServiceSuc(data),
        error => this.getLogFromShellRequestServiceErr(error)
      );
  }

  // UI UI
  initUICompleted() {
    LogService.info('UI init completed! ');
    // 
    this.authService.startAuth();
  }

  // 
  backMainMenu() {

    // 
    this.dialog.closeAll();

    // 
    this.userService.clearUserInfo();

    // 
    this.routerService.clearTrace();

    // UI
    this.routerService.goTo([PathLibrary.mainMenu]);
  }

  // 
  gotoErrorWait() {

    // 
    this.dialog.closeAll();

    // 
    this.userService.clearUserInfo();

    // 
    this.routerService.clearTrace();

    // UI
    this.routerService.goTo([PathLibrary.errorWait]);
  }

  //  ShellRequestService 
  private getLogFromShellRequestServiceSuc(infoMessage: InfoMessage): void {
    if (infoMessage.status === InfoMessage.statusNormal) {
      LogService.debug(infoMessage.data);
    } else {
      LogService.error(infoMessage.data);
    }
  }

  //  ShellRequestService 
  private getLogFromShellRequestServiceErr(errorMsg: string): void {
    LogService.error('getLogFromShellRequestService fail: [' + errorMsg + ']');
  }

}



